* {
  margin: 0;
}

body {
  background-color: rgb(221, 221, 221);
}

h2 {
  font-family: 'Muli', 'Arial', sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #340000;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
}

.app h1 {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 80px;
  font-family: 'Muli', 'Arial', sans-serif;
  text-transform: uppercase;
  color: #002134;
}

.app img {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  border-radius: 50%;
}

.app label {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 10px 0 5px 0;
  font-family: 'Muli', 'Arial', sans-serif;
  font-size: 22px;
}

.app input {
  display: -webkit-flex;
  display: flex;
  width: 180px;
  height: 25px;
  margin: 10px auto;
  padding-left: 10px;
  font-family: 'Muli', 'Arial', sans-serif;
  font-size: 14px;
  background: rgb(240, 240, 240);
  border: 1px solid rgb(145, 145, 145);
  border-radius: 4px;
}

.app input:focus {
  background: rgb(255, 255, 255);
  outline: none;
}

.app button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 60px;
  height: 25px;
  margin: 10px auto;
  font-family: 'Muli', 'Arial', sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #002134;
}

.app p {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 160px;
  margin: 10px auto;
  font-family: 'Muli', 'Arial', sans-serif;
  font-size: 12px;
  text-align: center;
  color: #500000;
}

.timeGone {
  background-color: #a61c00;
}

.checked {
  background-color: #d9ead3;
}

.checking {
  background-color: #ea9999;
}

.progress {
  background-color: #fff2cc;
}

.todo {
  background-color: #cccccc;
}

.tableScore {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.tableScore h2 {
  color: #002134;
}

.tableScore input {
  width: 200px;
  margin: 10px auto 20px auto;
}

.tableScore table {
  font-family: 'Muli', 'Arial', sans-serif;
  font-size: 14px;
  padding: 10px;
  background-color: #f0f0f0;
  border: 1px solid #919191;
  border-radius: 4px;
}

.tableScore table th {
  width: 150px;
  height: 20px;
}

.tableScore table td:first-child {
  width: 230px;
  height: 20px;
}

.tableScore a {
  text-decoration: none;
  color: #000000;
}

.tableScore a:hover {
  font-size: 16px;
  font-weight: 800px;
  text-decoration: none;
}

.tableScore a:active {
  text-decoration: none;
}

.tableScore a:visited {
  text-decoration: none;
}

.tableScore p {
  width: 240px;
}

